import React, { Component } from 'react';
import './App.css';
import RegistrationForm from './RegistrationForm';

import Backendless from 'backendless';
import LoginForm from "./LoginForm";
import ForgotPasswordForm from "./ForgotPasswordForm";
import {Route, Router, Routes} from "react-router-dom";
import Home from "./Home";

const APP_ID = process.env.REACT_APP_BACKENDLESS_APP_ID;
const API_KEY = process.env.REACT_APP_BACKENDLESS_SECRET_KEY;

Backendless.serverURL = 'https://eu-api.backendless.com';
Backendless.initApp(APP_ID, API_KEY);

const initialState = {
    loading: true,
    message: '',
    error  : null
}

class App extends Component {
    state = initialState

    render() {
        const { error, loading, message } = this.state

        return (
            <Router>
                <Routes>
                    <Route path="/" element={<Home/>}></Route>
                    <Route path="/register" element={<RegistrationForm/>}></Route>
                    <Route path="/login" element={<LoginForm/>}></Route>
                    <Route path="/password-reset" element={<ForgotPasswordForm/>}></Route>
                </Routes>
            </Router>
        );
    }
}

export default App;
